import { Component,ViewChild  } from '@angular/core';
import { IonicPage, NavController, NavParams,Events,ViewController } from 'ionic-angular';
import {FormGroup,FormControl} from '@angular/forms';
import { pageCommon } from '../../page.base';
/**
 * Generated class for the PrivacySettingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-privacy-setting',
  templateUrl: 'privacy-setting.html',
})
export class PrivacySettingPage extends pageCommon {
  @ViewChild('scroll') scroll; 
  people:string="公开";
  selected:any;

  constructor(public navCtrl: NavController, 
    public viewCtrl:ViewController,
    public navParams: NavParams,
    public events:Events
    ) {
    super(navCtrl)

    this.events.subscribe("privacy:selected",(data)=>{
      this.selected=data;
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PrivacySettingPage');
  }
  doSelect() {
    console.log(this.people);
  }
  doSubmit(){
    this.events.publish("privacy:done",this.people);
    this.viewCtrl.dismiss();
  }
  openContactChooser(){
    this.navCtrl.push("ContactChooserPage");
  }
  scrollWidth(){
    return super.scrollWidth()
  }

}



